@mixin s-font-face($font-family, $font-filename, $font-url:'', $font-weight: normal, $font-style: normal, $font-stretch: normal) {
    @font-face {
        font-family: '#{$font-family}';
        src:    url('#{$font-url}' + '#{$font-filename}.eot'); /* IE9 Compat Modes */
        src:    url('#{$font-url}' + '#{$font-filename}.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
                /* url('#{$font-url}' + '#{$font-filename}.woff2') format('woff2'), */ /* Modern Modern Browsers */
                url('#{$font-url}' + '#{$font-filename}.woff') format('woff'), /* Modern Browsers */
                url('#{$font-url}' + '#{$font-filename}.ttf')  format('truetype'), /* Safari, Android, iOS */
                url('#{$font-url}' + '#{$font-filename}.svg') format('svg'); /* Legacy iOS */

        font-style:   $font-style;
        font-weight:  $font-weight;
        font-stretch: $font-stretch;
    }
}
